.master-detail-layout {
  position: relative;
}

.detail-box {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: white;
  pointer-events: all;

  /* Hides the category select control
   * in the master column on mobile.
   */
  z-index: 2;
}


/*
 * Styles for tablet landscape and wider viewports.
 */
@media (min-width: 900px) {
  .master-detail-layout {
    display: flex;
  }

  .master-box {
    flex-shrink: 0;
    width: 30%;
    max-width: 280px;
  }

  .detail-box {
    position: static;
    flex-grow: 2;
    width: initial;
    height: initial;
  }
}
